/* src/components/NavHeader.css */
.nav-header {
    padding: 10px;
    background-color: #333;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo a {
    color: #fff;
    font-size: 1.5em;
    text-decoration: none;
}

.nav-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-around;
    width: 50%;
}

.nav-item {
    margin: 0;
}

.nav-link {
    color: #fff;
    text-decoration: none;
}

    .nav-link:hover {
        text-decoration: underline;
    }
